class Page1 extends React.Component {
  question = "В начале декабря прошлого года мы зашли в Ленту, чтобы купить что-то одно. Что именно?"
  correctExtra = "Да, пришли мы за тампонами, но по пути попалось много всего крутого!"
  imgCorrect = "https://github.com/gromakovsky/gromakovsky.github.io/raw/photos/photos/p1.correct.jpg"
  imgIncorrect = "https://github.com/gromakovsky/gromakovsky.github.io/raw/photos/photos/p1.incorrect.jpg"

  constructor(props) {
    super(props);
    this.state = {
      status: "init",
      failId: 0,
    }
  }

  render() {
    if (this.state.status === "init") {
      return (
        <div>
          {this.question}
          <br />
          <button onClick={() => this.setState({status: "incorrect", failId: 0})}>
            Чипсы
          </button>
          <button onClick={() => this.setState({status: "correct"})}>
            Тампоны
          </button>
          <br />
          <button onClick={() => this.setState({status: "incorrect", failId: 1})}>
            Печенье
          </button>
          <button onClick={() => this.setState({status: "incorrect", failId: 2})}>
            Шампанское
          </button>
        </div>
      )
    } else if (this.state.status === "incorrect") {
      var msg = "Без чипсов уйти из Ленты не так-то просто, но приходили не за ними.";
      if (this.state.failId === 1) {
        msg = "Печенье это норм тема, почему бы не взять? Но ответ неверный.";
      } else if (this.state.failId === 2) {
        msg =
          "Алкоголь мы редко покупаем, но тут попалось нечто годное с хорошей скидкой, грех не взять! Попробуйте снова."
      }
      return <PageIncorrect msg={msg} img={this.imgIncorrect}
        onBack={() => this.setState({status: "init"})} />;
    } else { // correct
      return <PageCorrect img={this.imgCorrect} extra={this.correctExtra}
        onNext={this.props.onNext} />;
    }
  }
}

