class Page3 extends React.Component {
  question = "Что нам делали 01.12.19?"
  answer = "массаж"
  correctExtra =
    "Умница Даша, всё верно, делали нам массаж. Это был подарок от коллег на ДР, который ты мне организовала. " +
    "Перед массажем мы были у родителей, а после массажа ты восхищалась силой массажисток. " +
    "Есть оттуда ещё пара фоточек, но я решил их не выкладывать в публично доступный сайт."
  imgCorrect = "https://github.com/gromakovsky/gromakovsky.github.io/raw/photos/photos/p3.correct.jpg"
  imgIncorrect = "https://github.com/gromakovsky/gromakovsky.github.io/raw/photos/photos/p3.incorrect.jpg"

  constructor(props) {
    super(props);
    this.state = {
      status: "init",
      value: "",
    }
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  handleSubmit(event) {
    if (this.state.value.toLowerCase() === this.answer) {
      this.setState({status: "correct"});
    } else {
      this.setState({status: "incorrect"});
    }
  }

  render() {
    if (this.state.status === "init") {
      return (
        <div>
          {this.question}
          <br />
          <input type="text" value={this.state.value} onChange={this.handleChange.bind(this)}></input>
          <br />
          <button
            onClick={this.handleSubmit.bind(this)}>
          Submit
          </button>
          <br />
          <button
            onClick={() => {
              this.setState({status: "hint"})
          }}>
          Показать подсказку
          </button>
        </div>
      )
    } else if (this.state.status === "hint") {
      return (
        <div>
          Это был подарок (мне/нам). За дополнительными подсказками можно обратиться напрямую к Ване.
          <br />
          <button
            onClick={() => {
              this.setState({status: "init"})
          }}>
          Вернуться к вопросу
          </button>
        </div>
      )
    } else if (this.state.status === "incorrect") {
      return <PageIncorrect img={this.imgIncorrect} msg="Ответ неверный :("
        onBack={() => this.setState({status: "init"})} />;
    } else { // correct
      return <PageCorrect img={this.imgCorrect} extra={this.correctExtra}
        onNext={this.props.onNext} />;
    }
  }
}


