class Page10 extends React.Component {
  question = "Удалось запечатлить первые поминки зелёных и длинных волос. Когда примерно это было?"
  correctExtra = "Да, а за пару часов до этого была фотка с икрой для мамы, где волосы ещё бесконечно длинные."
  imgCorrect = "https://github.com/gromakovsky/gromakovsky.github.io/raw/photos/photos/p10.correct.jpg"
  imgIncorrect = "https://github.com/gromakovsky/gromakovsky.github.io/raw/photos/photos/p10.incorrect.jpg"

  constructor(props) {
    super(props);
    this.state = {
      status: "init",
      failId: 0,
    }
  }

  render() {
    if (this.state.status === "init") {
      return (
        <div>
          {this.question}
          <br />
          <img src="https://github.com/gromakovsky/gromakovsky.github.io/raw/photos/photos/p10.jpg" width="500" alt=""/>
          <br />
          <button onClick={() => this.setState({status: "incorrect", failId: 0})}>
          начало 2021
          </button>
          <button onClick={() => this.setState({status: "incorrect", failId: 1})}>
          февраль 2021
          </button>
          <br />
          <button onClick={() => this.setState({status: "correct"})}>
          декабрь 2020
          </button>
          <button onClick={() => this.setState({status: "incorrect", failId: 2})}>
          осенью 2020
          </button>
        </div>
      )
    } else if (this.state.status === "incorrect") {
      var msg = "Не, в прошлом году это было.";
      if (this.state.failId === 1) {
        msg = "Не, в прошлом году это было.";
      } else if (this.state.failId === 2) {
        msg = "Не так рано."
      }
      return <PageIncorrect msg={msg} img={this.imgIncorrect}
        onBack={() => this.setState({status: "init"})} />;
    } else { // correct
      return <PageCorrect img={this.imgCorrect} extra={this.correctExtra}
        onNext={this.props.onNext} />;
    }
  }
}

class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      idx: 1,
    }
  }

  render() {
    if (this.state.idx === 1) {
      return <Page1 onNext={() => this.setState({idx: 2})} />
    } else if (this.state.idx === 2) {
      return <Page2 onNext={() => this.setState({idx: 3})}/>
    } else if (this.state.idx === 3) {
      return <Page3 onNext={() => this.setState({idx: 4})}/>
    } else if (this.state.idx === 4) {
      return <Page4 onNext={() => this.setState({idx: 5})}/>
    } else if (this.state.idx === 5) {
      return <Page5 onNext={() => this.setState({idx: 6})}/>
    } else if (this.state.idx === 6) {
      return <Page6 onNext={() => this.setState({idx: 7})}/>
    } else if (this.state.idx === 7) {
      return <Page7 onNext={() => this.setState({idx: 8})}/>
    } else if (this.state.idx === 8) {
      return <Page8 onNext={() => this.setState({idx: 9})}/>
    } else if (this.state.idx === 9) {
      return <Page9 onNext={() => this.setState({idx: 10})}/>
    } else if (this.state.idx === 10) {
      return <Page10 onNext={() => this.setState({idx: 11})}/>
    } else {
      return <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/e/ea/Thats_all_folks.svg/1019px-Thats_all_folks.svg.png"/>
    }
  }
}

// ========================================

ReactDOM.render(
  <Main />,
  document.getElementById('root')
);
